import { Component, OnDestroy, OnInit } from '@angular/core';
import { NbMenuService, NbSidebarService, NbThemeService } from '@nebular/theme';
import { AuthenticateService } from '../../../services/authentication/authenticate.service';
import { logoutConfirmAlert } from '../../../helpers/sweetalert';

import { Subject } from 'rxjs';

@Component({
  selector: 'ngx-header',
  styleUrls: ['./header.component.scss'],
  templateUrl: './header.component.html',
})
export class HeaderComponent implements OnInit, OnDestroy {

  private destroy$: Subject<void> = new Subject<void>();
  userPictureOnly: boolean = false;
  user: any;

  currentTheme = 'default';

  userMenu = [ { title: 'Settings', link: '/pages/settings', icon: 'settings-outline' }, { title: 'Log out', icon: 'unlock-outline' } ];

  constructor(
    private sidebarService: NbSidebarService,
    private menuService: NbMenuService,
    private themeService: NbThemeService,
    private authenticateService: AuthenticateService
  ) {}

  ngOnInit() {
    this.currentTheme = this.themeService.currentTheme;

    this.menuService.onItemClick().subscribe(async( event ) => {
      console.log('event.item.title', event.item.title);
      if(event.item.title === 'Log out' && await logoutConfirmAlert()){
        this.authenticateService.logout()
      }else if(event.item.title === 'Profile'){

      }
      
    })
  }

  ngOnDestroy() {
    this.destroy$.next();
    this.destroy$.complete();
  }

  toggleSidebar(): boolean {
    this.sidebarService.toggle(true, 'menu-sidebar');
    return false;
  }
}
