import { Injectable } from '@angular/core';
import { HttpRequest, HttpHandler, HttpEvent, HttpInterceptor } from '@angular/common/http';
import { Observable } from 'rxjs';

import { environment } from '../../environments/environment';
import { AuthenticateService } from '../services/authentication/authenticate.service';

@Injectable()
export class JwtInterceptor implements HttpInterceptor {
    constructor(private authenticateService: AuthenticateService) { }

    intercept(request: HttpRequest<any>, next: HttpHandler): Observable<HttpEvent<any>> {
        // add auth header with jwt if user is logged in and request is to the api url
        const currentUser = this.authenticateService.currentUserValue;
        const isLoggedIn = currentUser && currentUser.email;
        const storageValue = localStorage.getItem('currentUser') ? JSON.parse(localStorage.getItem('currentUser')!) : {};
        const isApiUrl = request.url.startsWith(environment.API_URL);
        
        if (isLoggedIn && isApiUrl && storageValue) {
            let token = storageValue.accessToken;
            request = request.clone({
                setHeaders: {
                    Authorization: `Bearer ${token}`
                }
            });
        }
        
        return next.handle(request);
    }
}